import { Add,Edit,Audit } from "@/api/shop/index.js"
import {
	EventBus
} from "@/common/bus.js";
const REQ = require("@/api/upload.js")
export default {
	data() {
		return {
			isShow:false,
			
			info:{
				logo: '',
				name:'',
				tag:'',
				type:[],
				share:'',
				intro:'',
			},
			desc:'',
			type:1,
			
			typeList: [{
					value: "1",
					label: "品牌商家",
					isSelected: false
				},
				{
					value: "2",
					label: "白菜商家",
					isSelected: false
				},
				{
					value: "3",
					label: "沧海遗珠",
					isSelected: false
				},
				{
					value: "4",
					label: "正品新店",
					isSelected: false
				},
				{
					value: "5",
					label: "定制商家",
					isSelected: false
				},
				{
					value: "6",
					label: "上新店铺",
					isSelected: false
				}
			]
		};
	},
	created() {
		
	},
	methods: {
		SetInfo(info){
			let types=info.type.split(",")
			this.info={
				id:info.id,
				logo: info.logo,
				name:info.name,
				tag:info.tag,
				type:types,
				share:info.share,
				intro:info.intro,
			}
			
			for (var i = 0; i < this.typeList.length; ++i) {
				this.typeList[i].isSelected = false;
				for (var j = 0; j < types.length; ++j) {
					if (this.typeList[i].value == types[j]) {
						this.typeList[i].isSelected = true;
						break
					}
				}
			}
		},
		auditData(){
			const data={
				id:this.info.id,
				desc:this.desc,
				type:this.type
			}
			uni.showLoading();
			Audit(data,info=>{
				uni.hideLoading();
				uni.showToast({
					icon:'none',
					title:info.message
				})
				uni.navigateBack();
				EventBus.$emit("reloadData-myShop-list");
			})
		},
		handleClick(type){
			const data={}
			this.type=type;
			
			if(type==1){
				//通过审核
				this.auditData();
			}else{
				//不通过审核
				this.isShow=true;
			}
		},
	},
}
